/* todo             Nav colors                   */
/* todo ——————————————————————————————————————————————————————————————————————————————— */
.dark-nav{
  background-color: #0e151a;
  color: skyblue;
  border-bottom: 0.1em solid skyblue;
  border-bottom: 0.1em solid white;
}

.light-nav{
  background-color: skyblue;
  color: #0e151a;
  border-bottom: 0.1em solid #0e151a;
}

.dark-navlink{ color: skyblue; }
.light-navlink{ color: #0e151a; }

.dark-navlink:hover{ border-bottom: 0.07em solid wheat; }
.light-navlink:hover{ border-bottom: 0.07em solid black; }


