/* todo ——————————————————————————————————————————————————————————————————————————————— */
.dropdown-background{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  
  display: flex;
  flex-direction: row-reverse;

  background: none;
}

.dropdown-content{
  display: flex;
  flex-direction: column;

  position: absolute;

  top: 3.5em;
  right: 0.5em;
  z-index: 12;
  
  width: 10em;
  /* height: 10em; */

  box-shadow: 0 0 8px gray;
  border-radius: 0.8em;

  overflow: hidden;
}

.dropdown-content > *{ padding: 1em; }

.dark-dropdownlink{ background-color: #0e151a; color: wheat; }
.light-dropdownlink{ background-color: wheat; }

.dark-dropdownlink:hover{ background-color: #19252e; }
.light-dropdownlink:hover{ background-color: #e6d0a8; }

